<template>
  <view>
    <!-- Ê†áÈ¢òÊ†è -->
    <navBar></navBar>

    <!-- ‰∏ªÂå∫Âüü -->
    <mainContainer>
      <view class="login-modules">
        <view class="mx-auto mt-40 my-30 icon-logo">
          <imgWithLoading :withoutDomainName="true" src="/static/icon/icon-logo.png"></imgWithLoading>
        </view>
        <view class="text-center big-title">Ë∂äÁæ§ËΩªÊùæËÆ¢ÁôªÂΩï</view>
        <view class="mt-40 mb-20 tips-title">Ë∂äÁæ§ËΩªÊùæËÆ¢Â∞èÁ®ãÂ∫èÁôªÂΩïÂ∞Ü‰ΩøÁî®Ôºö</view>
        <view class="get-info-list">
          <view class="d-flex align-items-center mb-20 get-info-item">‰Ω†ÁöÑÂÖ¨ÂºÄ‰ø°ÊÅØÔºàÊòµÁß∞„ÄÅÂ§¥ÂÉè„ÄÅÂú∞Âå∫ÂèäÊÄßÂà´Á≠âÔºâ</view>
        </view>
        <button class="com-big-btn" open-type="getUserInfo" @getuserinfo="triggerlogin">ÁôªÂΩï</button>
      </view>

      <bindMobileDialog ref="bindMobileDialog"></bindMobileDialog>
    </mainContainer>
  </view>
</template>

<script>
import bindMobileDialog from "@/components/bindMobileDialog.vue";

export default {
  components: {
    bindMobileDialog,
  },
  methods: {
    //Ëß¶ÂèëÁôªÂΩï
    async triggerlogin() {
      try {
        await uni.$checkTapTooFaster();

        if (this.isSubmitLogin === true) return false;
        this.isSubmitLogin = true;

        const ret_getUserInfo = await this.getUserInfoByWx(); //Ëé∑ÂèñÂæÆ‰ø°Áî®Êà∑‰ø°ÊÅØ
        const ret_doWxLogin = await this.doWxLogin(); //ÂæÆ‰ø°ÁôªÂΩï
        const { msg, res: loginRet } = await this.loginByAjax(
          ret_doWxLogin,
          ret_getUserInfo
        ); //Êé•Âè£ÁôªÂΩï

        await this.saveMyLoginInfo(loginRet); //‰øùÂ≠òÊàëÁöÑÁôªÂΩï‰ø°ÊÅØ

        const isBindMobile = await this.isBindMobileByAjax(); //ÊòØÂê¶ÁªëÂÆö‰∫ÜÊâãÊú∫Âè∑

        //Ê≤°ÊúâÁªëÂÆöÊâãÊú∫
        if (!isBindMobile) {
          this.isSubmitLogin = false;

          if (this.isBindedMobile !== true) {
            this.isBindedMobile = await new Promise((resolve) => {
              this.$refs.bindMobileDialog.render({
                success: resolve,
              });
            });
          }

          await uni.$com.getAndBindUserClient(); //ÁªëÂÆöÁî®Êà∑ÁöÑÂÆ¢Êà∑(Á±ª‰ºº‰∫éÂ§öËßíËâ≤ÔºåÈÄâ‰∏™ËßíËâ≤)
        }

        console.info("ret_getUserInfo", ret_getUserInfo);
        console.info("ret_doWxLogin", ret_doWxLogin);
        console.info("loginRet", loginRet);

        await this.loginSuccess(loginRet);

        uni.$toast({
          msg,
        });
      } catch (error) {
        if (error && error.errMsg === "getUserInfo:fail:auth deny") {
          //Áî®Êà∑ÁÇπÂáªÂèñÊ∂àÂæÆ‰ø°ÊéàÊùÉÊåâÈíÆ
          return false;
        }

        delete this.isSubmitLogin;
        uni.$catchError(error);

      }
    },

    //‰øùÂ≠òÊàëÁöÑÁôªÂΩï‰ø°ÊÅØ
    saveMyLoginInfo(loginRet) {
      uni.setStorageSync("userInfo", loginRet);
      uni.setStorageSync("token_outTime", new Date().getTime());
      uni.setStorageSync("token", loginRet.token);
    },

    //È™åËØÅÊòØÂê¶ÁªëÂÆö‰∫ÜÊâãÊú∫Âè∑
    async isBindMobileByAjax() {
      const { res } = await uni.$ajax({
        apiKey: "bindMobile",
        data: {
          check_bind: 1, //È™åËØÅÊòØÂê¶ÁªëÂÆöÊâãÊú∫Âè∑Á†Å 1ÊòØ
        },
      });

      return !!(res && res.status);
    },

    //Ëé∑ÂèñÁî®Êà∑‰ø°ÊÅØ
    getUserInfoByWx() {
      return new Promise((resolve, reject) => {
        uni.getUserInfo({
          withCredentials: "true",
          lang: "zh_CN",
          timeout: 10000,
          success: (result) => {
            resolve(result.userInfo);
          },
          fail: (err) => {
            console.error("err", err);

            if (err.err_code == -12006 || /-12006/.test(err.errMsg)) {
              //Áî®Êà∑‰∏çÂêåÊÑèÊéàÊùÉ
              err = "ÊúçÂä°ÈúÄË¶ÅÔºåËØ∑ÂêåÊÑèÊéàÊùÉ";
            }
            reject(err);
          },
        });
      });
    },

    //ÂæÆ‰ø°ÁôªÂΩï
    doWxLogin() {
      return new Promise((resolve, reject) => {
        uni.login({
          success: (result) => {
            resolve(result);
          },
          fail: (error) => reject(error),
        });
      });
    },

    //Êé•Âè£ÁôªÂΩï
    async loginByAjax(ret_doWxLogin, ret_getUserInfo) {
      const { code: js_code } = ret_doWxLogin;
      const {
        avatarUrl: avatar,
        nickName: nickname,
        gender,
        country,
        province,
        city,
      } = ret_getUserInfo;

      return await uni.$ajax({
        apiKey: "login",
        data: {
          js_code, //  js_code jsÊéàÊùÉÁ†Å
          nickname, //  nickname üòé ÂæÆ‰ø°ÊòµÁß∞
          avatar, //  avatar Â§¥ÂÉè
          gender, //  gender ÊÄßÂà´ 0‰øùÂØÜ 1Áî∑ 2Â•≥
          country, //  country ÂõΩÂÆ∂
          province, //  province ÁúÅ‰ªΩ
          city, //  city ÂüéÂ∏Ç
        },
      });
    },

    async loginSuccess(res) {
      // await uni.setStorageSync('userInfo', res);
      // await uni.setStorageSync('token_outTime', new Date().getTime());
      // await new Promise(resolve => setTimeout(() => resolve()), 0);
      // await uni.setStorageSync('token', res.token);
      // await new Promise(resolve => setTimeout(() => resolve()), 0);

      //ÂàùÂßãÂåñÊù•Ê∫êÈ°µ
      const pages = await getCurrentPages();
      const lastPage = pages[pages.length - 2];

      if (lastPage) {
        pages
          .slice(0, pages.length - 1)
          .forEach((item) => item.onLoad(item.options || ""));

        await uni.$router.back(-1); //ÂêéÈÄÄ‰∏ÄÈ°µ
      } else {
        // await uni.$router.back('userCenter'); //v1ÔºåÂè™ËøîÂõûÂà∞‰∏™‰∫∫‰∏≠ÂøÉ
        uni.$router.back("home"); //v2ÁâàÊú¨
      }

      uni.$bus.emit("loginSuccess");
    },
  },
};
</script>

<style lang="scss" scoped>
.login-modules {
  padding-left: 66rpx;
  padding-right: 66rpx;
}

.login-modules .icon-logo {
  width: 150rpx;
  height: 150rpx;
}
.login-modules .big-title {
  padding-bottom: 50rpx;
  border-bottom: 1px solid #cccccc;
  font-weight: bold;
  font-size: 16px;
  color: #333;
}
.login-modules .tips-title {
  font-weight: bold;
  font-size: 16px;
  color: #333;
}

.login-modules .get-info-list {
  margin-bottom: 80rpx;
}
.login-modules .get-info-list .get-info-item::before {
  margin-right: 26rpx;
  content: "";
  display: block;
  width: 10rpx;
  height: 10rpx;
  background-color: #bbbbbb;
  border-radius: 50%;
}
.login-modules .get-info-list .get-info-item {
  font-size: 14px;
  color: #606060;
}
</style>